
#app {
  width: 80%;
  max-width: 1176px;
  margin: auto;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.gallery__header {
  padding-bottom: 72px;
  text-align: left;
}
.gallery__header > h1 {
  font-family: 'Courgette';
  font-size: 36px;
  font-weight: 400;
  color: #111;
}
.gallery__header > p {
  font-weight: 400;
  line-height: 1.6;
}
.gallery__zoom-in {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 6;
  background-position: 50%;
  background-size: cover;
  cursor: zoom-out;
  transition: transform 0.35s cubic-bezier(0.77, 0, 0.175, 1), -webkit-transform 0.35s cubic-bezier(0.77, 0, 0.175, 1);
}
.gallery__layout {
  margin-bottom: 24px;
  text-align: right;
}
.gallery__layout__btn {
  width: 20px;
  fill: #808080;
  margin: 0 10px;
  cursor: pointer;
}
.gallery__layout__btn:hover {
  fill: #111;
}
.gallery__layout__btn--active {
  fill: #111;
}


